
import Namespace from "./Namespace";
import ContainerNamespace from "./ContainerNamespace";
import ListNamespace from "./ListNamespace";
import InspectorFrameNamespace from "./InspectorFrameNamespace";

import {DEFAULT_FRAMES_LIMIT, DEFAULT_TRACEBACK_LIMIT} from "./V8StackNamespace";

// Note - this is a serious duplication of the V8StackNamespace
export default class InspectorStackNamespace extends Namespace {

    constructor(inspector, callFrames) {
        super();

        this.inspector = inspector;
        this.callFrames = callFrames;
    }

    readKey(key) {
        return new InspectorFrameNamespace(this.inspector, this.callFrames[key]);
    }

    callMethod(name, args) {
        switch (name) {
            case "traceback":
                return this.traceback(args);
            case "frames":
                return this.frames(args);
            default:
                return super.callMethod(name, args);
        }
    }

    traceback(args) {
        // Decide on dump depth
        let depth = DEFAULT_TRACEBACK_LIMIT;
        if (null != args && '' !== args) {
            depth = parseInt(args);
        }
        if (depth > this.callFrames.length) {
            depth = this.callFrames.length;
        }

        let result = [];
        for (let i = 0; i < depth; ++i) {
            let frame = new InspectorFrameNamespace(this.inspector, this.callFrames[i]);

            result.push(new ContainerNamespace({
                module: frame.module(),
                filename: frame.filename(),
                line: frame.line(),
                function: frame.function()
            }));
        }

        return new ListNamespace(result);
    }

    frames(args) {
        // Decide on dump depth
        let depth = DEFAULT_FRAMES_LIMIT;
        if (null != args && '' !== args) {
            depth = parseInt(args);
        }
        if (depth > this.callFrames.length) {
            depth = this.callFrames.length;
        }

        let result = [];
        for (let i = 0; i < depth; ++i) {
            let frame = new InspectorFrameNamespace(this.inspector, this.callFrames[i]);

            result.push(frame.callMethod('dump'));
        }

        return new ListNamespace(result);
    }

}
